export function SkeletonLine({ className = "" }: { className?: string }) {
  return <div className={`h-3 bg-gray-200/80 rounded animate-pulse ${className}`} />;
}

export function SkeletonCard() {
  return (
    <div className="bg-white/80 dark:bg-slate-900/50 p-4 shadow rounded-2xl space-y-2 animate-pulse">
      <SkeletonLine className="h-4 w-2/3" />
      <SkeletonLine className="w-1/2" />
      <SkeletonLine className="w-1/3" />
    </div>
  );
}

export function SkeletonList({ rows = 4, className = "" }: { rows?: number; className?: string }) {
  return (
    <div className={`space-y-2 ${className}`}>
      {Array.from({ length: rows }).map((_, idx) => (
        <div key={`skeleton-row-${idx}`} className="bg-white/70 dark:bg-slate-900/40 border border-slate-100 dark:border-slate-800 rounded-xl p-3 space-y-2 animate-pulse">
          <SkeletonLine className="w-2/3" />
          <SkeletonLine className="w-1/3" />
        </div>
      ))}
    </div>
  );
}

export function SkeletonTableRows({ rows = 4 }: { rows?: number }) {
  return (
    <div className="space-y-2">
      {Array.from({ length: rows }).map((_, idx) => (
        <div key={`skeleton-table-${idx}`} className="grid grid-cols-12 gap-2 items-center">
          <SkeletonLine className="col-span-3 h-3" />
          <SkeletonLine className="col-span-4 h-3" />
          <SkeletonLine className="col-span-3 h-3" />
          <SkeletonLine className="col-span-2 h-3" />
        </div>
      ))}
    </div>
  );
}
