"use client";
import { getBrowserApiBase } from "@/lib/api";
import Button from "@/components/Button";
import Panel from "@/components/Panel";
import { formatBDT } from "@/lib/format";
import { useEffect, useMemo, useState } from "react";
import { SkeletonList } from "@/components/Skeleton";
import { useLockBodyScroll } from "@/hooks/useLockBodyScroll";

type User = {
  id: string;
  name: string;
  email?: string;
  role: "admin" | "accountant" | "user";
  balance?: number;
  lastMonth?: number;
};

export default function PeopleClient({ initialUsers, isAdmin }: { initialUsers: User[]; isAdmin: boolean }) {
  const [query, setQuery] = useState("");
  const [baseUsers, setBaseUsers] = useState<User[]>(initialUsers);
  const [users, setUsers] = useState<User[]>(initialUsers);
  const [loading, setLoading] = useState(false);
  const [form, setForm] = useState({ name: "", email: "", role: "user" });
  const [editing, setEditing] = useState<User | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [searching, setSearching] = useState(false);
  useLockBodyScroll(Boolean(editing));

  const fetchAllUsers = async () => {
    const res = await fetch(`${getBrowserApiBase()}/users`, { credentials: "include" });
    if (!res.ok) throw new Error("Failed to load members");
    const data = await res.json();
    setBaseUsers(data);
    if (!query.trim()) setUsers(data);
    return data;
  };

  useEffect(() => {
    const trimmed = query.trim();
    if (!trimmed) {
      setSearching(false);
      setUsers(baseUsers);
      return;
    }
    const controller = new AbortController();
    setSearching(true);
    const handle = setTimeout(async () => {
      try {
        const res = await fetch(`${getBrowserApiBase()}/users?q=${encodeURIComponent(trimmed)}`, {
          credentials: "include",
          signal: controller.signal,
        });
        if (!res.ok) throw new Error("Failed to search members");
        const data = await res.json();
        setUsers(data);
      } catch (err: any) {
        if (err?.name !== "AbortError") setError(err?.message || "Failed to search members");
      } finally {
        setSearching(false);
      }
    }, 250);
    return () => {
      controller.abort();
      clearTimeout(handle);
    };
  }, [query, baseUsers]);

  const handleCreate = async () => {
    if (!form.name.trim()) return;
    setLoading(true);
    setError(null);
    try {
      const res = await fetch(`${getBrowserApiBase()}/users`, {
        method: "POST",
        credentials: "include",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ ...form, password: "ChangeMe123!" }),
      });
      if (!res.ok) throw new Error((await res.json())?.error || "Failed to create");
      setForm({ name: "", email: "", role: "user" });
      setQuery("");
      await fetchAllUsers();
    } catch (err: any) {
      setError(err?.message || "Failed to create");
    } finally {
      setLoading(false);
    }
  };

  const handleSave = async () => {
    if (!editing) return;
    setLoading(true);
    setError(null);
    try {
      const res = await fetch(`${getBrowserApiBase()}/users/${editing.id}`, {
        method: "PATCH",
        credentials: "include",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ name: editing.name, email: editing.email, role: editing.role }),
      });
      if (!res.ok) throw new Error((await res.json())?.error || "Failed to update");
      setEditing(null);
      await fetchAllUsers();
    } catch (err: any) {
      setError(err?.message || "Failed to update");
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async (id: string) => {
    if (!confirm("Delete this member?")) return;
    setLoading(true);
    setError(null);
    try {
      const res = await fetch(`${getBrowserApiBase()}/users/${id}`, {
        method: "DELETE",
        credentials: "include",
      });
      if (!res.ok) throw new Error((await res.json())?.error || "Failed to delete");
      await fetchAllUsers();
    } catch (err: any) {
      setError(err?.message || "Failed to delete");
    } finally {
      setLoading(false);
    }
  };

  const filtered = useMemo(() => users, [users]);

  return (
    <div className="space-y-6">
      <div className="flex flex-col gap-2">
        <input
          className="w-full rounded-xl border border-slate-200 dark:border-slate-700 bg-white/80 dark:bg-slate-900/60 px-4 py-2"
          placeholder="Search members…"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
        />
        {error ? <p className="text-sm text-rose-500">{error}</p> : null}
        {loading ? <p className="text-xs text-slate-500">Loading…</p> : null}
      </div>

      {isAdmin && (
        <Panel
          title="Add someone new"
          description="Admins can invite new members or staff."
          actions={
            <Button onClick={handleCreate} disabled={!form.name.trim() || loading}>
              Add member
            </Button>
          }
        >
          <div className="grid grid-cols-1 sm:grid-cols-3 gap-3">
            <div className="space-y-2">
              <label className="text-sm text-slate-600 dark:text-slate-300">Full name</label>
              <input
                className="input-like"
                value={form.name}
                onChange={(e) => setForm((prev) => ({ ...prev, name: e.target.value }))}
              />
            </div>
            <div className="space-y-2">
              <label className="text-sm text-slate-600 dark:text-slate-300">Email</label>
              <input
                className="input-like"
                value={form.email}
                onChange={(e) => setForm((prev) => ({ ...prev, email: e.target.value }))}
              />
            </div>
            <div className="space-y-2">
              <label className="text-sm text-slate-600 dark:text-slate-300">Role</label>
              <select
                className="input-like"
                value={form.role}
                onChange={(e) => setForm((prev) => ({ ...prev, role: e.target.value }))}
              >
                <option value="user">Member</option>
                <option value="accountant">Accountant</option>
                <option value="admin">Admin</option>
              </select>
            </div>
          </div>
        </Panel>
      )}

      <Panel title="Everyone in the circle" description={`${filtered.length} member${filtered.length === 1 ? "" : "s"}`}>
        {searching ? (
          <SkeletonList rows={3} />
        ) : (
          <div className="space-y-3">
            {filtered.map((user) => (
              <div
                key={user.id}
                className="rounded-2xl border border-slate-200 dark:border-slate-800 p-4 flex flex-col gap-2 bg-white/90 dark:bg-slate-900/60"
              >
                <div className="flex items-center justify-between">
                  <div>
                    <p className="font-semibold text-slate-900 dark:text-white">{user.name}</p>
                    <p className="text-xs text-slate-500 dark:text-slate-400">{user.email || "No email"}</p>
                  </div>
                  <div className="text-right text-sm text-slate-500 dark:text-slate-400">
                    <div>Balance {formatBDT(user.balance || 0)}</div>
                    <div>Last month {formatBDT(user.lastMonth || 0)}</div>
                  </div>
                </div>
                {isAdmin ? (
                  <div className="flex gap-2 justify-end">
                    <Button variant="ghost" onClick={() => setEditing(user)}>
                      Edit
                    </Button>
                    <Button variant="danger" onClick={() => handleDelete(user.id)}>
                      Delete
                    </Button>
                  </div>
                ) : null}
              </div>
            ))}
            {!filtered.length && <p className="text-sm text-slate-500">No members match your search.</p>}
          </div>
        )}
      </Panel>

      {editing ? (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/10" onClick={() => setEditing(null)}>
          <div
            className="w-full max-w-md rounded-2xl border border-slate-200 dark:border-slate-700 bg-white/85 dark:bg-slate-950/75 backdrop-blur-sm p-5 space-y-3 transform-gpu transition"
            onClick={(e) => e.stopPropagation()}
          >
            <h3 className="text-lg font-semibold text-slate-900 dark:text-white">Edit member</h3>
            <input
              className="input-like"
              value={editing.name}
              onChange={(e) => setEditing((prev) => (prev ? { ...prev, name: e.target.value } : prev))}
            />
            <input
              className="input-like"
              value={editing.email || ""}
              onChange={(e) => setEditing((prev) => (prev ? { ...prev, email: e.target.value } : prev))}
            />
            <select
              className="input-like"
              value={editing.role}
              onChange={(e) => setEditing((prev) => (prev ? { ...prev, role: e.target.value as User["role"] } : prev))}
            >
              <option value="user">Member</option>
              <option value="accountant">Accountant</option>
              <option value="admin">Admin</option>
            </select>
            <div className="flex justify-end gap-2 pt-2">
              <Button variant="ghost" onClick={() => setEditing(null)}>
                Cancel
              </Button>
              <Button onClick={handleSave} disabled={loading}>
                Save changes
              </Button>
            </div>
          </div>
        </div>
      ) : null}
    </div>
  );
}
